@import '../../assets/scss/com.scss';
.search{
    width: 100%;
    height: 100%;
    .search_list{
        width: 100%;
        height: 100%;
        background-color: #fff;
        .list_top{
            width: 100%;
            height: 80px;
            text-indent: 30px;
            font-size: 32px;
            display: flex;
            align-items: center;
            background-color: #F1F0F5;
        }
    }
    .search_null{
        width: 100%;
        height:800px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .null_text{
            width: 250px;
            text-align: center;
            font-size: 28px;
            font-family: Microsoft YaHei;
            font-weight: bold;
            color: #BBBBBB;
            margin-bottom: 47px;
        }
        .null_img{
            width: 250px;
            display: flex;
            justify-content: space-between;
            .img_line{
                display: flex;
                flex-direction: column;
                align-items: center;
                img{
                    width: 29px;
                    height: 35px;
                }
                span{
                    padding-top: 10px;
                    display: block;
                    font-size: 20px;
                    font-family: Microsoft YaHei;
                    font-weight: 400;
                    color: #BBBBBB;
                    box-sizing: border-box;
                }
            }
        }
    }
}