.list{
    width: 100%;
    height: 133px;
    display: flex;
    align-items: center;
    padding: 0px 29px;
    box-sizing: border-box;
    border-bottom: 1px solid #C9C9C9;
    .list_left{
        padding-right: 18px;
        box-sizing: border-box;
    }
    .list_right{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        span{
            display: block;
            font-size: 28px;
            font-family: Microsoft YaHei;
            font-weight: 400;
            box-sizing: border-box;
            color: #363636;
        }
        .right_span{
            margin-top: 10px;
        }
    }
}